.header {
  background-image: url("background-banner.b7987623.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0 0 120px;
  position: relative;
}

@media (width <= 1024px) {
  .header {
    padding: 0 0 126px;
  }
}

@media (width <= 320px) {
  .header {
    padding: 0 0 52px;
  }
}

.header:after {
  content: "";
  background-color: #252b49cc;
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
}

.navigation {
  margin-bottom: 130px;
}

.navigation-wrap {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

@media (width <= 320px) {
  .navigation-wrap {
    flex-direction: column;
    padding: 10px 0;
  }
}

.navigation-wrap:after {
  content: "";
  background-color: #babbc5;
  width: 100%;
  height: 1px;
  display: block;
  position: absolute;
  top: 100%;
}

.navigation-logo__left-part {
  color: #656980;
  font-family: Raleway;
  font-size: 1.5rem;
  font-weight: 900;
  line-height: 1.76125rem;
}

.navigation-logo__right-part {
  color: #fff;
  font-family: Raleway;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.76125rem;
}

.navigation-active ul {
  transition: all .5s;
  position: relative;
  transform: translate(0);
}

.navigation-active .navigation-burger .fa-bars {
  display: none;
}

@media (width <= 320px) {
  .navigation-active .navigation-burger .fa-xmark {
    color: #fff;
    cursor: pointer;
    width: 19px;
    height: 19px;
    display: block;
  }
}

.fa-bars {
  display: none;
}

@media (width <= 320px) {
  .fa-bars {
    color: #fff;
    cursor: pointer;
    width: 24px;
    height: 18px;
    display: block;
  }
}

.fa-xmark {
  display: none;
}

@media (width <= 320px) {
  .navigation-back {
    justify-content: space-between;
    align-items: center;
    width: 100%;
    display: flex;
  }
}

.navigation-list {
  justify-content: space-between;
  width: 670px;
